import React from 'react';
import classNames from 'classnames';
import './CompleteButton.scss';

type Props = {
  title: string,
  isDisabled?: boolean,
};

export const CompleteButton: React.FC<Props> = ({ title, isDisabled }) => {
  return (
    <button
      type="submit"
      className={classNames(
        'completeButton',
        { 'completeButton-disable': !isDisabled },
      )}
    >
      {title}
    </button>
  );
};
